/* About Hero */
.about-hero {
  background: linear-gradient(135deg, rgba(0, 90, 115, 0.05), rgba(0, 167, 181, 0.05));
  text-align: center;
  padding-top: 120px;
}

.about-hero-content {
  max-width: 800px;
  margin: 0 auto;
}

/* Mission Section */
.mission {
  background: var(--white);
}

.mission-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 4rem;
  align-items: center;
}

.mission-text h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 1.5rem;
}

.mission-text p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: var(--gray);
  margin-bottom: 1.5rem;
}

.mission-stats {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.mission-stats .stat-item {
  text-align: center;
  padding: 1.5rem;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  border-radius: var(--border-radius);
  color: var(--white);
}

.mission-stats .stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.mission-stats .stat-label {
  font-size: 1rem;
  opacity: 0.9;
}

/* Values Section */
.values {
  background: var(--light-gray);
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.value-card {
  text-align: center;
  padding: 2.5rem 2rem;
}

.value-icon {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1.5rem;
  font-size: 2rem;
  color: var(--white);
}

.value-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 1rem;
}

.value-card p {
  color: var(--gray);
  line-height: 1.6;
}

/* Team Section */
.team {
  background: var(--white);
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.team-card {
  text-align: center;
  padding: 2rem;
}

.team-image {
  margin-bottom: 1.5rem;
}

.placeholder-image {
  width: 120px;
  height: 120px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  font-size: 3rem;
  color: var(--white);
}

.team-info h3 {
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--dark-gray);
  margin-bottom: 0.5rem;
}

.team-role {
  display: block;
  color: var(--primary-color);
  font-weight: 500;
  margin-bottom: 1rem;
}

.team-info p {
  color: var(--gray);
  line-height: 1.6;
}

/* Story Section */
.story {
  background: linear-gradient(135deg, var(--accent-color), var(--primary-color));
  color: var(--white);
}

.story-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.story-content h2 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.story-content p {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 1.5rem;
  opacity: 0.9;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .mission-content {
    grid-template-columns: 1fr;
    gap: 3rem;
    text-align: center;
  }
  
  .mission-stats {
    flex-direction: row;
    justify-content: center;
  }
  
  .mission-stats .stat-item {
    flex: 1;
    max-width: 200px;
  }
}

@media (max-width: 768px) {
  .about-hero {
    padding-top: 100px;
  }
  
  .mission-text h2 {
    font-size: 2rem;
  }
  
  .mission-text p {
    font-size: 1rem;
  }
  
  .mission-stats {
    flex-direction: column;
    gap: 1rem;
  }
  
  .mission-stats .stat-item {
    max-width: none;
  }
  
  .story-content h2 {
    font-size: 2rem;
  }
  
  .story-content p {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .mission-text h2 {
    font-size: 1.8rem;
  }
  
  .value-card {
    padding: 2rem 1.5rem;
  }
  
  .value-icon {
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
  }
  
  .placeholder-image {
    width: 100px;
    height: 100px;
    font-size: 2.5rem;
  }
  
  .story-content h2 {
    font-size: 1.8rem;
  }
} 