/* ===== HEADER COMPONENT STYLES ===== */
/* These styles create the navigation bar that appears at the top of every page */

/* Main header container */
.header {
  position: fixed;              /* Stays at top of screen when scrolling */
  top: 0;                      /* Position at very top */
  left: 0;                     /* Position at very left */
  right: 0;                    /* Extend to very right */
  background: rgba(255, 255, 255, 0.98); /* More opaque white background */
  backdrop-filter: blur(15px);  /* Enhanced blur effect */
  z-index: 1000;               /* Ensures header appears above other content */
  transition: all 0.3s ease;   /* Smooth transitions for all changes */
  border-bottom: 1px solid rgba(0, 0, 0, 0.08); /* Subtle bottom border */
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.08); /* Subtle shadow */
}

/* Header when user has scrolled down */
.header.scrolled {
  background: rgba(255, 255, 255, 0.99); /* More opaque when scrolled */
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.12); /* Enhanced shadow when scrolled */
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); /* Slightly more visible border */
}

/* Container for header content */
.header-container {
  max-width: var(--container-max-width); /* Maximum width */
  margin: 0 auto;              /* Center the container */
  padding: 0 var(--spacing-md); /* Horizontal padding */
  display: flex;               /* Use flexbox for layout */
  align-items: center;         /* Center items vertically */
  justify-content: space-between; /* Space items evenly */
  height: 70px;               /* Slightly smaller height for better fit */
}

/* Logo section - positioned completely to the left */
.logo {
  display: flex;               /* Use flexbox for logo layout */
  align-items: center;         /* Center logo and text vertically */
  gap: var(--spacing-sm);     /* Space between logo image and text */
  text-decoration: none;       /* Remove underline from link */
  color: var(--primary-color); /* Brand color for logo text */
  font-weight: 700;           /* Bold font weight */
  font-size: 1.4rem;         /* Slightly smaller font size */
  transition: all var(--transition-normal); /* Smooth transitions */
  margin-right: auto;         /* Push logo to the left */
  flex-shrink: 0;             /* Prevent logo from shrinking */
}

/* Logo hover effect */
.logo:hover {
  color: var(--secondary-color); /* Change color on hover */
  transform: scale(1.02);      /* Slight scale effect */
}

/* Logo image */
.logo img {
  height: 250px;               /* Slightly smaller height for better fit */
  width: auto;                /* Maintain aspect ratio */
}

/* Desktop navigation menu */
.nav-desktop {
  display: flex;               /* Use flexbox for menu items */
  gap: var(--spacing-md);     /* Space between menu items */
  align-items: center;         /* Center items vertically */
  margin-left: auto;          /* Push navigation to the right */
}

/* Desktop navigation links */
.nav-desktop a {
  text-decoration: none;       /* Remove underline */
  color: var(--dark-gray);    /* Text color */
  font-weight: 500;           /* Medium font weight */
  padding: var(--spacing-sm) var(--spacing-md); /* Internal spacing */
  border-radius: var(--border-radius-md); /* Rounded corners */
  transition: all var(--transition-normal); /* Smooth transitions */
  position: relative;         /* For hover effects */
  font-size: 0.75rem;        /* Slightly smaller font size */
  letter-spacing: 0.3px;     /* Letter spacing for better readability */
}

/* Desktop navigation link hover effects */
.nav-desktop a:hover {
  color: var(--primary-color); /* Change color on hover */
  background-color: rgba(0, 90, 115, 0.08); /* Light background on hover */
  transform: translateY(-1px); /* Slight lift effect */
}

/* Active navigation link (current page) */
.nav-desktop a.active {
  color: var(--primary-color); /* Brand color for active link */
  background-color: rgba(0, 90, 115, 0.12); /* Light background for active link */
  font-weight: 600;           /* Semi-bold for active link */
}

/* Highlighted navigation link (Inquire Now) */
.nav-desktop a.highlighted {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color)); /* Gradient background */
  color: var(--white);        /* White text */
  font-weight: 600;           /* Semi-bold font weight */
  padding: var(--spacing-sm) var(--spacing-lg); /* More padding */
  border-radius: var(--border-radius-lg); /* More rounded corners */
  box-shadow: 0 4px 15px rgba(0, 90, 115, 0.3); /* Enhanced shadow */
  position: relative;         /* For pseudo-element */
  overflow: hidden;           /* Hide overflow for pseudo-element */
}

/* Highlighted link hover effect */
.nav-desktop a.highlighted:hover {
  transform: translateY(-2px); /* More lift effect */
  box-shadow: 0 6px 20px rgba(0, 90, 115, 0.4); /* Enhanced shadow on hover */
  color: var(--white);        /* Keep white text on hover */
}

/* Highlighted link active state */
.nav-desktop a.highlighted.active {
  background: linear-gradient(135deg, var(--secondary-color), var(--primary-color)); /* Reverse gradient for active */
  box-shadow: 0 6px 20px rgba(0, 90, 115, 0.5); /* Enhanced shadow for active */
}

