/* Mobile menu button - hidden on desktop */
.mobile-menu-btn {
  display: none;               /* Hidden by default (desktop) */
  background: none;            /* No background */
  border: none;               /* No border */
  font-size: 1.3rem;         /* Icon size */
  color: var(--primary-color); /* Brand color */
  cursor: pointer;            /* Show pointer cursor */
  padding: var(--spacing-sm); /* Internal spacing */
  border-radius: var(--border-radius-md); /* Rounded corners */
  transition: all var(--transition-normal); /* Smooth transitions */
}

/* Mobile menu button hover effect */
.mobile-menu-btn:hover {
  background-color: rgba(0, 90, 115, 0.08); /* Light background on hover */
  transform: scale(1.05);     /* Slight scale effect */
}

/* Mobile navigation menu */
.mobile-menu {
  position: fixed;             /* Fixed position */
  top: 70px;                  /* Position below header */
  left: 0;                    /* Start from left edge */
  right: 0;                   /* Extend to right edge */
  background: var(--white);    /* White background */
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15); /* Enhanced shadow for depth */
  padding: var(--spacing-lg); /* Internal spacing */
  display: flex;              /* Use flexbox for layout */
  flex-direction: column;     /* Stack items vertically */
  gap: var(--spacing-md);    /* Space between items */
  z-index: 999;              /* Below header but above other content */
  border-top: 1px solid rgba(0, 0, 0, 0.08); /* Top border */
}

/* Mobile navigation links */
.mobile-menu a {
  text-decoration: none;       /* Remove underline */
  color: var(--dark-gray);    /* Text color */
  font-weight: 500;           /* Medium font weight */
  padding: var(--spacing-sm) var(--spacing-md); /* Internal spacing */
  border-radius: var(--border-radius-md); /* Rounded corners */
  transition: all var(--transition-normal); /* Smooth transitions */
  display: block;             /* Full width */
  font-size: 1rem;           /* Font size */
  letter-spacing: 0.3px;     /* Letter spacing */
}

/* Mobile navigation link hover effects */
.mobile-menu a:hover {
  color: var(--primary-color); /* Change color on hover */
  background-color: rgba(0, 90, 115, 0.08); /* Light background on hover */
  transform: translateX(5px); /* Slight slide effect */
}

/* Active mobile navigation link */
.mobile-menu a.active {
  color: var(--primary-color); /* Brand color for active link */
  background-color: rgba(0, 90, 115, 0.12); /* Light background for active link */
  font-weight: 600;           /* Semi-bold for active link */
}

/* Highlighted mobile navigation link */
.mobile-menu a.highlighted {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color)); /* Gradient background */
  color: var(--white);        /* White text */
  font-weight: 600;           /* Semi-bold font weight */
  text-align: center;         /* Center text */
  margin: var(--spacing-sm) 0; /* Vertical margin */
}

/* Mobile contact information */
.mobile-contact {
  border-top: 1px solid rgba(0, 0, 0, 0.08); /* Top border separator */
  padding-top: var(--spacing-md); /* Top padding */
  margin-top: var(--spacing-md); /* Top margin */
  display: flex;              /* Use flexbox for layout */
  flex-direction: column;     /* Stack items vertically */
  gap: var(--spacing-sm);    /* Space between items */
}

/* Mobile contact item */
.mobile-contact .contact-item {
  display: flex;              /* Use flexbox for icon and text */
  align-items: center;        /* Center icon and text vertically */
  gap: var(--spacing-xs);    /* Space between icon and text */
  color: var(--gray);        /* Text color */
  font-size: 0.9rem;        /* Font size */
  padding: var(--spacing-sm) 0; /* Vertical padding */
}

/* ===== RESPONSIVE DESIGN ===== */
/* Media queries for different screen sizes */

/* For tablets and smaller desktops */
@media (max-width: 1024px) {
  .header-container {
    height: 65px;             /* Slightly smaller height */
  }
  
  .nav-desktop {
    gap: var(--spacing-sm);   /* Less space between menu items */
  }
  
  .nav-desktop a {
    font-size: 0.9rem;       /* Smaller font size */
    padding: var(--spacing-xs) var(--spacing-sm); /* Less padding */
  }
  
  .nav-desktop a.highlighted {
    padding: var(--spacing-xs) var(--spacing-md); /* Less padding for highlighted */
  }
  
  .contact-info {
    gap: var(--spacing-sm);   /* Less space between contact items */
  }
  
  .contact-item {
    font-size: 0.8rem;       /* Smaller font size */
  }
  
  .mobile-menu {
    top: 65px;               /* Position below smaller header */
  }
}

/* For mobile devices */
@media (max-width: 768px) {
  .header-container {
    height: 60px;             /* Even smaller height on mobile */
  }
  
  .logo {
    font-size: 1.2rem;       /* Smaller logo text */
  }
  
  .logo img {
    height: 250px;            /* Smaller logo image */
  }
  
  /* Hide desktop navigation and contact info on mobile */
  .nav-desktop,
  .contact-info {
    display: none;
  }
  
  /* Show mobile menu button on mobile */
  .mobile-menu-btn {
    display: block;
  }
  
  .mobile-menu {
    top: 60px;               /* Position below smaller header */
  }
}

/* For very small mobile devices */
@media (max-width: 480px) {
  .header-container {
    padding: 0 var(--spacing-sm); /* Less padding on very small screens */
  }
  
  .logo {
    font-size: 1rem;         /* Even smaller logo text */
  }
  
  .logo img {
    height: 250px;   
    padding-left: 0;
    margin-left: 0;         /* Even smaller logo image */
  }
  
  .mobile-menu {
    padding: var(--spacing-md); /* Less padding */
  }
  
  .mobile-menu a {
    font-size: 0.95rem;      /* Smaller font size */
  }
} 